import { Injectable } from '@angular/core';
import { UtilService } from '../common/util.service';

@Injectable()
export class FridgeService {

  constructor(private us: UtilService) { }

  fridgeServiceSubscription = function (obj, urlConf, powerSubject, doorSubject, bulbSubject) {

    let debug = false;
    let config;
    
    if(urlConf.environment){
      config = obj.window.createConfig(urlConf.environment);
    } else {
      config = obj.window.createConfig();
      if(urlConf.token){
        config.token = urlConf.token;
      }

      if(urlConf.proxyUrl){
        config.url = urlConf.proxyUrl;
      }
    }
    
    var smartFridge = obj.window.createSmartFridge(config);

    smartFridge.on('error', err => {
      console.error(">>> SmartShelf Service Error:", err);
    });

    smartFridge.on('shelfData', data => {
      var newShelf = this.us.newShelf(data);
      this.us.updateShelves(obj, newShelf);
    });

    smartFridge.on('scaleData', data => {
    });

    smartFridge.on('doorData', data => {
      doorSubject.next(data.doorOpened);
    });

    smartFridge.on('powerData', data => {
      powerSubject.next(Math.round(data.powerConsumptionMilliWatts/1000 * 100) / 100);
    });
    
    smartFridge.on('bulbData', data => {
      var hexColor = this.us.rgbToHex(data.color.r, data.color.g, data.color.b);
      obj.colorPicker.setColorByHex(hexColor);
      obj.lightOn = (hexColor != '#000000');
    });

    bulbSubject.subscribe({
      next:function (value) {
        obj.lightOn = (value != '#000000');
        smartFridge.setBulbColor(value);
      }
    });

  };
}



